/** reset **/
body {
	font-family: "helvetica neue",arial,sans-serif;
	font-size: 14px;
	color: #444;
}

/** main navbar **/
.top-navbar {
	min-height: 54px;
	margin-bottom: 0px;
	border-top: 1px solid #e6e6e6;
	border-bottom: 1px solid #e6e6e6;
	background: #fff;
	background: rgba(255,255,255,.85);
}

.top-navbar a.idea-logo {
	line-height: 48px;
	padding: 9px 15px;
}

.top-navbar .navbar-nav > li > a {
	line-height: 24px;
	color: #5F5F5F;
	padding-left: 15px;
	padding-right: 15px;
}

.top-navbar .navbar-nav > li > a > span.glyphicon {
	margin-right: 2px;
}

.top-navbar .navbar-nav > .active > a, .top-navbar .navbar-nav > li > a:hover, .top-navbar  .navbar-nav > .open > a:hover {
	color: #e9573e;
	background-color: transparent;
}

.top-navbar .navbar-nav > .open > a, .top-navbar .navbar-nav > .open > a:hover, .top-navbar .navbar-nav > .open > a:focus {
	color: #e9573e;
	background-color: rgba(255,255,255,.85);
}

.top-navbar .dropdown-menu > .active > a, .dropdown-menu > .active > a:hover, .dropdown-menu > .active > a:focus {
	color: #e9573e;
	background-color: transparent;
}

.top-navbar .navbar-nav > li.active > a:after {
	content: "";
	display: block;
	position: absolute;
	left: -1px;
	right: -1px;
	bottom: -1px;
	border-top: 1px solid #EA714C;
}

.top-navbar .dropdown-menu > li > a {
	color: #5F5F5F;
}

.top-navbar .dropdown-menu > li > a:hover, .top-navbar .dropdown-menu > li > a:focus {
	color: #e9573e;
	text-decoration: none;
	background: transparent;
}

.top-navbar .navbar-nav > li.login-box > a {
	float: left;
	font-size: 13px;
	border-right: 1px solid #e6e6e6;
}

.top-navbar img.logo_img {
	margin-top: -3px;
}

/** main content **/
.main {
	background: #ebeced;
	min-height: 500px;
	padding: 20px 0;
}

/** footer **/
.footer {
	width: 100%;
	background-color: #fff;
	background-color: rgba(255, 255, 255, 0.85098);
	padding: 20px 10px;
}

.footer p {
	color: #666;
}

.footer a {
	color: #666;
}

/** goto top **/
.gotop_box {
	position:fixed;
	bottom:45px;
	right:20px;
	width:50px;
	display:block;
}

.gotop_box a {
	display: block;
	width:50px;
	height:50px;
	margin-bottom:5px;
	border-radius:3px;
}

.gotop_box a.gotop {
	background:#ddd url(http://res.htwed.com/v4.0.0/img/gotop.png) no-repeat 0 0;
}
.gotop_box a.gotop:hover {
	background:#e9573e url(http://res.htwed.com/v4.0.0/img/gotop.png) no-repeat 0 0;
}
.gotop_box a.goweibo {
	background:#ddd url(http://res.htwed.com/v4.0.0/img/gotop.png) no-repeat 0 -50px;
}
.gotop_box a.goweibo:hover {
	background:#e9573e url(http://res.htwed.com/v4.0.0/img/gotop.png) no-repeat 0 -50px;
}
.gotop_box a.goweixin {
	background:#ddd url(http://res.htwed.com/v4.0.0/img/gotop.png) no-repeat 0 -100px;
}
.gotop_box a.goweixin:hover {
	background:#e9573e url(http://res.htwed.com/v4.0.0/img/gotop.png) no-repeat 0 -100px;
}

.goweixin:hover .ewm-div {
	display: block;
}

.ewm-div {
	background: url("http://res.htwed.com/v4.0.0/img/ewm.png") no-repeat scroll 4px 7px #FFF;
	border: 1px solid #E0E0E0;
	display: none;
	height: 138px;
	left: -290px;
	position: absolute;
	top: -60px;
	width: 290px;
}

/** login modal **/
#login-modal {
	padding-top: 10%;
	text-align: center;
	font-size: 14px;
}

#login-modal .login .logo {
	margin: 15px 0 10px;
}

#login-modal  .login .words {
	font-size: 16px;
	color: #999;
	margin-bottom: 15px;
}

#login-modal .socialLogin {
	margin-bottom: 15px;
}

#login-modal .socialLogin a {
	display: inline-block;
	margin-right: 20px;
	width: 70px;
	height: 70px;
	background: url(http://res.htwed.com/ideas/img/login_icons.png) 0 0 no-repeat;
}

#login-modal .socialLogin a.weibo:hover {
	background-position: 0 -90px;
}

#login-modal .socialLogin a.wechat {
	background-position: -90px 0;
}

#login-modal .socialLogin a.wechat:hover {
	background-position: -90px -90px;
}

#login-modal .socialLogin a.douban {
	background-position: -180px 0;
}

#login-modal .socialLogin a.renren {
	background-position: -270px 0;
}

#login-modal .userLogin {
	width: 280px;
	text-align: left;
	position: relative;
	margin: 0 auto 15px;
	border: 0;
}

#login-modal .userLogin .with-line {
	font-size: 16px;
	color: #999;
	margin: 0 auto;
	position: relative;
	text-align: center;
}

#login-modal .userLogin .with-line:before {
	content: "";
	border-top: 1px solid #ededed;
	display: block;
	position: absolute;
	width: 80px;
	top: 11px;
	left: 0;
}

#login-modal .userLogin .with-line:after {
	content: "";
	border-top: 1px solid #ededed;
	display: block;
	position: absolute;
	width: 80px;
	top: 11px;
	right: 0;
}

#login-modal .mail-login {
	margin: 15px auto;
}

#login-modal .mail-login input {
	margin-bottom: 10px;
	width: 100%;
	font-size: 16px;
	box-sizing: border-box;
}

#login-modal .reset-password {
	display: inline-block;
	cursor: pointer;
	color: #9A0000;
}

#login-modal .switch-back {
	position: absolute;
	right: 0;
	bottom: 0;
	color: #444;
}

#login-modal .switch-back a {
	cursor: pointer;
	color: #9A0000;
}

@media (min-width: 768px) {
	#login-modal .modal-dialog {
		position: relative;
		width: 520px;
		margin: 30px auto;
	}
}

