/** jquery tags **/
div.tagsinput { border:1px solid #CCC; background: #FFF; padding:5px; width:300px; height:100px; overflow-y: auto;}
div.tagsinput span.tag { border: 1px solid #a5d24a; -moz-border-radius:2px; -webkit-border-radius:2px; display: block; float: left; padding: 5px; text-decoration:none; background: #cde69c; color: #638421; margin-right: 5px; margin-bottom:5px;font-family: helvetica;  font-size:13px;}
div.tagsinput span.tag a { font-weight: bold; color: #82ad2b; text-decoration:none; font-size: 11px;  } 
div.tagsinput input { width:80px; margin:0px; font-family: helvetica; font-size: 13px; border:1px solid transparent; padding:5px; background: transparent; color: #000; outline:0px;  margin-right:5px; margin-bottom:5px; }
div.tagsinput div { display:block; float: left; } 
.tags_clear { clear: both; width: 100%; height: 0px; }
.not_valid {background: #FBD8DB !important; color: #90111A !important;}


/** ideas cate navbar **/
.idea-navbar {
	margin-bottom: 10px;
}
.idea-navbar .idea-cates {
	line-height: 32px;
	position: relative;
	z-index: 1;
	margin-top: 5px;
	background: #fff;
	background: rgba(255, 255, 255, 0.85098);
}

.idea-navbar .idea-cates:last-child {
	border-bottom: 1px solid #DDD;
}

.idea-navbar .idea-cates .active, .idea-navbar .idea-cates a:hover {
	color: #e9573e;
}

.idea-navbar .idea-cates a {
	display: inline-block;
	padding: 0 12px;
	font-size: 14px;
	color: #5F5F5F;
	position: relative;
	text-decoration: none;
}

@media (min-width: 768px) {
	.idea-navbar .idea-cates {
		line-height: 50px;
	}
	
	.idea-navbar .idea-cates a {
		padding: 0 12px;
	}
}

@media (min-width: 992px) {
	.idea-navbar .idea-cates {
		line-height: 50px;
	}
	
	.idea-navbar .idea-cates a {
		padding: 0 15px;
	}
}

@media (min-width: 1200px) {
	.idea-navbar .idea-cates {
		line-height: 50px;
	}

	.idea-navbar .idea-cates a {
		padding: 0 15px;
	}
}

/** ideas css **/
.ideas .result-filter {
	margin: 10px 0 0px;
	position: relative;
	z-index: 1;
}

.cate-pics {
	margin-top: 10px;
	margin-left: -10px;
	margin-right: 0;
}



/** pic page **/
.pic-page .pic-info .detail h1 {
	font-size: 24px;
	font-weight: bold;
	line-height: 32px;
}

.pic-page .pic-info .detail h3 {
	font-size: 18px;
	font-weight: bold;
	line-height: 24px;
}

.pic-page .pic-info .detail .user {
	padding-top: 14px;
}

.pic-page .pic-info .detail .user p {
	margin-left: 72px;
}

.pic-page .pic-info .detail .user .avatar32 {
	width: 50px;
	height: 50px;
	float: left;
}

.pic-page .pic-info .detail .user .text {
	line-height: 20px;
	margin-top: 3px;
}

.pic-page .pic-info .detail .btn-box {
	border-top: 1px dotted #ccc;
	padding-top: 24px;
	margin-top: 20px;
}

.pic-page .pic-info .detail .btn-box a {
	cursor: pointer;
	font-size: 16px;
}

.pic-page .pic-info .detail .btn-box i {
	font-size: 18px;
	line-height: 18px;
	padding-right: 5px;
}

.pic-page .pic-info .detail .share {
	margin-top: 20px;
}

.pic-page .pic-info .detail .share .share-btn {
	cursor: pointer;
	float: left;
	height: 18px;
	position: relative;
	line-height: 18px;
	padding-right: 10px;
}

.pic-page .pic-info .detail .share i {
	color: #f69;
}

.pic-page .pic-container .pic-box {
	text-align: center;
	margin-bottom: 30px;
}

.pic-page .pic-container .pic-comments {
	clear: both;
	position: relative;
	z-index: 11;
	margin-bottom: 67px;
	padding-top: 30px;
}

.pic-page .pic-container .pic-comments h4 {
	position: relative;
	text-align: center;
	margin-bottom: 50px;
	height: 25px;
	line-height: 25px;
}

.pic-page .pic-container .pic-comments h4:after {
	content: '';
	position: absolute;
	top: 12px;
	left: 0;
	right: 0;
	height: 0;
	border-top: 1px solid #ddd;
	overflow: hidden;
}

.pic-page .pic-container .pic-comments h4 span{
	position: relative;
	z-index: 1;
	padding: 0 20px;
	font-weight: 100;
	font-size: 22px;
	color: #888;
}

@media (min-width: 768px) {
	.pic-page .pic-info .detail h1 {
		font-size: 18px;
	}
}

@media (min-width: 992px) {
	.pic-page .pic-info .detail h1 {
		font-size: 20px;
	}
}

@media (min-width: 1200px) {
	.pic-page .pic-info .detail h1 {
		font-size: 24px;
		font-weight: bold;
		line-height: 32px;
	}
}


