/** fullscreen pics slider **/
.slideContainerOuter {	
	position: absolute;
	left: 0;
	right: 0;
	height: 560px;
	z-index: 101;
	top: -10000px;
	overflow: hidden;
}

.slideContainerOuter.active {
	top: 80px!important;
	position: fixed!important;
}

.fullPicsSlideContainer {
	position: relative;
	width: 770px;
	height: 560px;
	margin: 0 auto;
	overflow: visible;
}
.slideItemContainer .iosSliderContainer {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 560px;
	margin: 0 0 0 0;
	overflow: visible;
}

.wpslide {
	padding: 14px;
	width: 748px;
	height: 100%;
	float: left;
	background: #fff;
	margin: 0 15px;
	box-shadow: 0 0 10px rgba(0,0,0,0.20);
}

.wpslide > .img {
	float: left;
	width: 720px;
	height: 480px;
	position: relative;
	text-align: center;
}

.totallike-count {
	width: 130px;
	height: 75px;
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -37px 0 0 -60px;
	display: none;
	text-align: center;
}

.ioscontrol {
	width: 46px;
	height: 70px;
	border-radius: 3px;
	position: absolute;
	top: 50%;
	left: -77px;
	margin-top: -35px;
	cursor: pointer;
}


.s2 {
	background: url(/img/sprite2.png) no-repeat;
	float: left;
}

.iosleft {
	background-position: -10px -10px;
	width: 29px;
	height: 50px;
}

.ioscontrol .iosleft {
	margin: 12px 0 0 13px;
}

#nextSlide {
	left: auto;
	right: -83px;
}

.iosright {
	background-position: -50px -10px;
	width: 29px;
	height: 50px;
}

.ioscontrol .iosright {
	margin: 12px 0 0 7px;
}

.fade-close {
	position: fixed;
	z-index: 1;
	right: 40px;
	top: 16px;
	width: 120px;
	height: 40px;
	font-size: 32px;
	cursor: pointer;
	background-position: 0 -300px;
}

.fade-close:hover {
	color: white;
}

/* waterfall css */
.item-container .item {
	margin-bottom: 20px;
	padding: 0 10px;
}

/** search */
.top-navbar .navbar-search {
	border: 1px solid #ddd;
	border-radius: 4px;
	padding-right: 1px;
}

.top-navbar .navbar-search input {
  border: none;
  box-shadow: none;
  display: inline-block;
  padding: 0;
  background: transparent;
}

.top-navbar .navbar-search .search-btn {
  color: #aaa;
  border-radius: 3px;
  font-size: 18px;
  padding: 2px 3px 3px 8px;
  border: none;
  -webkit-transition: all 200ms ease-in-out;
  -moz-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
}

.top-navbar .navbar-search .search-btn:hover {
  color: #fff;
  background-color: #e9573e;
}